<template>
  <div class="js-scenarist-child">
    <vx-header :showback="1" :title="$t('common.cut')"></vx-header>
    <div class="vx-container">
      <div class="interception">
        <div class="inputSource" ref="inputSource">
          <div class="cutsource" ref="cutSource"></div>
        </div>
      </div>
      <div class="progress-list">
        <vx-progress :title="interception.width.title" :num="interception.width.value" @update="widthChange" :range="interception.width.range" :step="interception.width.step" @touchend="save"></vx-progress>
        <vx-progress :title="interception.height.title" :num="interception.height.value" @update="heightChange" :range="interception.height.range" :step="interception.height.step" @touchend="save"></vx-progress>
        <vx-progress :title="interception.x.title" :num="interception.x.value" @update="xChange" :range="interception.x.range" :step="interception.x.step" @touchend="save"></vx-progress>
        <vx-progress :title="interception.y.title" :num="interception.y.value" @update="yChange" :range="interception.y.range" :step="interception.y.step" @touchend="save"></vx-progress>
      </div>
    </div>
  </div>
</template>
<script>
  import { serialize } from 'src/utils';
  import htp from "../../api/http";
  import url from '../../api/apiurl';
  export default {
    data() {
      return {
        //宽高XY进度条配置
        interception: {
          width: {
            title: this.$t('common.width'),
            value: 0,
            step: 1,
            range: {
              start: 64,
              end: this.sourceWidth
            }
          },
          height: {
            title: this.$t('common.height'),
            value: 0,
            step: 1,
            range: {
              start: 64,
              end: this.sourceHeight
            }
          },
          x: {
            title: this.$t('common.X'),
            value: 0,
            step: 1,
            range: {
              start: 0,
              end: this.sourceWidth
            }
          },
          y: {
            title: this.$t('common.Y'),
            value: 0,
            step: 1,
            range: {
              start: 0,
              end: this.sourceHeight
            }
          }
        },
        winid: 0,//窗口Id
        inputSourceId: 0,//源Id
        source: { width: 0, height: 0, x: 0, y: 0 },
        inputSourceStyle: {
          widthRate: 0,
          heightRate: 0,
          sourceWidth: 1920,
          sourceHeight: 1080
        }
      };
    },
    mounted() {
      this.winid = this.$route.params.winId;
      this.inputSourceId = this.$route.params.inputSourceId;
      this.getInputSources();
    },
    methods: {
      getInputSources() {
        htp.all(
          htp.get(url.getInputSourceList),
          htp.post(url.getInputSourceCrop, { selectWindow: parseInt(this.winid), selectInput: parseInt(this.inputSourceId) })
        ).then(res => {
          if(res[0].status === 10000000 && res[1].status === 10000000) {
            let source = res[0].data.inputSourceList.filter(inputSource => {
              return inputSource.id === parseInt(this.$route.params.inputSourceId);
            });
            let cropSource = res[1].data;
            if(source.length) {
              this.inputSourceStyle.sourceWidth = parseInt(source[0].width) ? parseInt(source[0].width) : 1920;
              this.inputSourceStyle.sourceHeight = parseInt(source[0].height) ? parseInt(source[0].height) : 1080;
              this.inputSourceStyle.widthRate = this.$refs.inputSource.getBoundingClientRect().width / this.inputSourceStyle.sourceWidth;
              this.inputSourceStyle.heightRate = this.$refs.inputSource.getBoundingClientRect().height / this.inputSourceStyle.sourceHeight;
            }

            // this.$refs.inputSource.style.width = Math.ceil(this.inputSourceStyle.sourceWidth * this.inputSourceStyle.widthRate) + 'px';
            // this.$refs.inputSource.style.height = Math.ceil(this.inputSourceStyle.sourceHeight * this.inputSourceStyle.heightRate) + 'px';

            //设置W,H,X,Y的最大值
            this.interception.width.range.end = this.inputSourceStyle.sourceWidth;
            this.interception.x.range.end = this.inputSourceStyle.sourceWidth - this.interception.width.range.start;
            this.interception.height.range.end = this.inputSourceStyle.sourceHeight;
            this.interception.y.range.end = this.inputSourceStyle.sourceHeight - this.interception.height.range.start;
            console.log(cropSource);
            //设置W,H,X,Y的当前值

            this.interception.width.value = this.inputSourceStyle.sourceWidth < cropSource.cropWidth ? this.inputSourceStyle.sourceWidth : cropSource.cropWidth;

            this.interception.height.value = this.inputSourceStyle.sourceHeight < cropSource.cropHeight ? this.inputSourceStyle.sourceHeight : cropSource.cropHeight;

            this.interception.x.value = cropSource.cropStartX;
            this.interception.y.value = cropSource.cropStartY;
            //设置当前源在屏幕中的宽度高度偏移量
            this.$refs.cutSource.style.width = Math.ceil(this.interception.width.value * this.inputSourceStyle.widthRate) + 'px';
            this.$refs.cutSource.style.height = Math.ceil(this.interception.height.value * this.inputSourceStyle.heightRate) + 'px';
            this.$refs.cutSource.style.left = Math.ceil(this.interception.x.value * this.inputSourceStyle.widthRate) + 'px';
            this.$refs.cutSource.style.top = Math.ceil(this.interception.y.value * this.inputSourceStyle.heightRate) + 'px';
          }
        });
      },
      widthChange(val) {
        this.interception.width.value = val;
        this.$refs.cutSource.style.width = Math.ceil(this.interception.width.value * this.inputSourceStyle.widthRate) + 'px';
        if(this.interception.x.value + this.interception.width.value > this.inputSourceStyle.sourceWidth) {
          this.interception.x.value = this.inputSourceStyle.sourceWidth - this.interception.width.value;
        }
      },
      heightChange(val) {
        this.interception.height.value = val;
        this.$refs.cutSource.style.height = Math.ceil(this.interception.height.value * this.inputSourceStyle.heightRate) + 'px';
        if(this.interception.y.value + this.interception.height.value > this.inputSourceStyle.sourceHeight) {
          this.interception.y.value = this.inputSourceStyle.sourceHeight - this.interception.height.value;
        }
      },
      xChange(val) {
        this.interception.x.value = val;
        this.$refs.cutSource.style.left = Math.ceil(this.interception.x.value * this.inputSourceStyle.widthRate) + 'px';
        console.log(this.inputSourceStyle.sourceWidth);
        console.log(this.interception.width.value + this.interception.x.value);
        if(this.interception.width.value + this.interception.x.value > this.inputSourceStyle.sourceWidth) {
          this.interception.width.value = this.inputSourceStyle.sourceWidth - this.interception.x.value;
        }
      },
      yChange(val) {
        this.interception.y.value = val;
        this.$refs.cutSource.style.top = Math.ceil(this.interception.y.value * this.inputSourceStyle.heightRate) + 'px';
        if(this.interception.height.value + this.interception.y.value >= this.inputSourceStyle.sourceHeight) {
          this.interception.height.value = this.inputSourceStyle.sourceHeight - this.interception.y.value;
        }
      },
      save() {
        htp.post(url.setInputSourceCrop,
          {
            cropHeight: this.interception.height.value,
            cropWidth: this.interception.width.value,
            cropStartX: this.interception.x.value,
            cropStartY: this.interception.y.value,
            windowId: parseInt(this.winid),
            inputSourceId: parseInt(this.inputSourceId)
          });
      },
      cancel() {
        this.$router.go(-1);
      }
    }
  };
</script>
<style scoped lang="less">
  .vx-container {
    background-color: #212124;
  }

  @media screen and (orientation: portrait) {
    .vx-container {
      top: 100px;
      .interception {
        width: 100%;
        height: 400px;
        margin: 0 auto;
        border: 2px solid #3d3d3d;
        border-radius: 6px;
        margin-top: 20px;
        flex: 1;
        .inputSource {
          width: 400px;
          height: 260px;
          margin: 0 auto;
          margin-top: 5%;
          border: 2px dashed #525252;
          border-radius: 6px;
          position: relative;
          box-sizing: border-box;
          .cutsource {
            position: absolute;
            border: 2px solid #fff;
            box-sizing: border-box;
          }
        }
      }
      .progress-list {
        background-color: #383940;
        text-align: left;
        font-size: 24px;
        margin-top: 40px;
        line-height: 60px;
        text-indent: 35px;
        width: 100%;
        .vx-progress {
          margin-top: 0;
          padding-top: 20px;
          color: #a8a8aa;
        }
      }
    }
  }
  @media screen and (orientation: landscape) {
    .vx-container {
      display: flex;
      .interception {
        width: 100%;
        flex: 1;
        height: 400px;
        margin: 80px 30px;
        border: 2px solid #3d3d3d;
        border-radius: 6px;
        margin-top: 20px;
        .inputSource {
          width: 400px;
          height: 260px;
          margin: 0 auto;
          margin-top: 5%;
          border: 2px dashed #525252;
          border-radius: 6px;
          position: relative;
          box-sizing: border-box;
          .cutsource {
            position: absolute;
            border: 2px solid #fff;
            box-sizing: border-box;
          }
        }
      }
      .progress-list {
        flex: 1;
        background-color: #383940;
        text-align: left;
        font-size: 24px;
        margin-top: 10px;
        line-height: 60px;
        text-indent: 35px;
        width: 100%;
        .vx-progress {
          margin-top: 0;
          padding-top: 20px;
          color: #a8a8aa;
        }
      }
    }
  }
</style>


